import WSBox from "components/WSBox";
import WSTypography from "components/WSTypography";
import Icon from "@mui/material/Icon";
import TimelineItem from "modules/Timeline/TimelineItem";
import Card from "@mui/material/Card";
import {CardMedia} from "@mui/material";
import brucemars from "assets/images/bruce-mars.jpg";
import Divider from "@mui/material/Divider";
function Introduction(){
    const styles = {
        media: {
            height: "100%",
            padding: "56.25px", // 16:9,
            boxShadow:"10px 10px 5px 0px rgba(0,0,0,0.75)",
            opacity:"0.95"
        }
    };
    return(
        <Card style={styles.media}>
            <WSTypography color={"error"} sx={{fontFamily: `Georgia`,fontWeight:"bold",fontSize:"XXL"}}>Introduction</WSTypography>
            <WSTypography sx={{fontFamily: "Georgia",fontWeight:"bold"}}>Hello, My Name is Mehrdad Kheirmand.</WSTypography>
            <WSTypography sx={{fontFamily: `Helvetica`,marginBottom:"10px"}}>I'm an experienced computer engineer and full-stack developer with nearly 5 years of expertise in crafting digital experiences.
                At the intersection of technology and creativity,
                I bring a passion for innovative solutions
                and the ability for turning ideas into reality. </WSTypography>
            <WSTypography color={"error"} sx={{fontFamily: `Georgia`,fontWeight:"bold",fontSize:"XXL"}}>Projects That Define Me</WSTypography>
            <WSTypography sx={{fontFamily: `Helvetica`}}>
                🌐 Web Wonders: Explore the interactive landscapes of web applications where user experience is paramount.
                From elegant e-commerce platforms to dynamic content delivery,
                I've created digital spaces that leave a lasting impression.</WSTypography>

            <Divider/>
            <WSTypography  sx={{fontFamily: `Helvetica`}}>🤖 AI Marvels: Venture into the fascinating world of artificial intelligence.
                My projects in machine learning and image processing showcase a commitment
                to harnessing technology for practical and evocative applications.</WSTypography>
        </Card>
    );
}


export default Introduction;